.drawer__menu {
  list-style: none;
  padding: 0;
  min-width: 160px;
  max-width: 160px; }
  .drawer__menu__link {
    width: 100%;
    border-left: 4px solid rgba(0, 0, 0, 0);
    cursor: pointer; }
    .drawer__menu__link:hover {
      background: rgba(0, 0, 0, 0.02); }
    .drawer__menu__link-active {
      border-left: 4px solid gray; }
    .drawer__menu__link a {
      margin: auto;
      padding: 10px 0;
      min-width: 120px;
      max-width: 120px;
      height: 100%;
      display: flex;
      align-items: center;
      text-decoration-line: none; }
      .drawer__menu__link a p {
        font-size: 20px;
        margin-left: 15px; }
      .drawer__menu__link a svg {
        margin-top: 2px;
        fill: gray; }
