.about {
  background: linear-gradient(182deg, #ececec, #ffffff, #dbf7ff);
  background-size: 600% 600%;
  -webkit-animation: gradient_flux 10s ease infinite;
  -moz-animation: gradient_flux 10s ease infinite;
  animation: gradient_flux 10s ease infinite;
  position: fixed;
  padding: 0px;
  margin: 0px;
  width: 100%;
  height: 100%; }
  .about::before {
    content: '';
    height: 0px;
    padding: 0px;
    position: absolute;
    left: 50%;
    top: 100%;
    z-index: 2;
    display: block;
    -webkit-border-radius: 50%;
    border-radius: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-animation: puff_portrait 0.5s 1.8s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, borderRadius 0.2s 2.3s linear forwards;
    animation: puff_portrait 0.5s 1.8s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, borderRadius 0.2s 2.3s linear forwards; }
  .about h1 {
    font-size: 24px;
    font-weight: bold; }
  .about h2 {
    font-weight: 400;
    font-size: 16px; }
  .about p {
    margin: 0px; }
  .about .profile-card {
    background: #ffb300;
    width: 56px;
    height: 56px;
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 2;
    overflow: hidden;
    opacity: 0;
    margin-top: 70px;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-border-radius: 50%;
    border-radius: 50%;
    -webkit-box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16), 0px 3px 6px rgba(0, 0, 0, 0.23);
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16), 0px 3px 6px rgba(0, 0, 0, 0.23);
    -webkit-animation: init 0.5s 0.2s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, moveDown 1s 0.8s cubic-bezier(0.6, -0.28, 0.735, 0.045) forwards, moveUp 1s 1.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards, materia_landscape 0.5s 2.7s cubic-bezier(0.86, 0, 0.07, 1) forwards;
    animation: init 0.5s 0.2s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, moveDown 1s 0.8s cubic-bezier(0.6, -0.28, 0.735, 0.045) forwards, moveUp 1s 1.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards, materia_landscape 0.5s 2.7s cubic-bezier(0.86, 0, 0.07, 1) forwards; }
  .about .profile-card header {
    width: 179px;
    height: 400px;
    padding: 40px 20px 30px 20px;
    display: inline-block;
    border-right: 2px dashed #eeeeee;
    background: #ffffff;
    color: #000000;
    margin-top: 50px;
    opacity: 0;
    text-align: center;
    -webkit-animation: moveIn 1s 3.1s ease forwards;
    animation: moveIn 1s 3.1s ease forwards; }
  .about .profile-card header h1 {
    font-size: 30px;
    color: #234579; }
  .about .profile-card header h2 {
    color: #1d2f3f; }
  .about .profile-card header a {
    display: inline-block;
    text-align: center;
    position: relative;
    margin: 25px 30px; }
  .about .profile-card header a:after {
    position: absolute;
    content: '';
    bottom: 3px;
    right: 3px;
    width: 20px;
    height: 20px;
    border: 4px solid #ffffff;
    -webkit-transform: scale(0);
    transform: scale(0);
    background: -webkit-linear-gradient(top, #2196f3 0%, #2196f3 50%, #ffc107 50%, #ffc107 100%);
    background: linear-gradient(#2196f3 0%, #2196f3 50%, #ffc107 50%, #ffc107 100%);
    -webkit-border-radius: 50%;
    border-radius: 50%;
    -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
    -webkit-animation: scaleIn 0.3s 3.5s ease forwards;
    animation: scaleIn 0.3s 3.5s ease forwards; }
  .about .profile-card header a > img {
    width: 120px;
    max-width: 100%;
    -webkit-border-radius: 50%;
    border-radius: 50%;
    -webkit-transition: -webkit-box-shadow 0.3s ease;
    transition: box-shadow 0.3s ease;
    -webkit-box-shadow: 0px 0px 0px 8px rgba(0, 0, 0, 0.06);
    box-shadow: 0px 0px 0px 8px rgba(0, 0, 0, 0.06); }
  .about .profile-card header a:hover > img {
    -webkit-box-shadow: 0px 0px 0px 12px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 0px 12px rgba(0, 0, 0, 0.1); }
  .about .profile-card .profile-bio {
    width: 175px;
    height: 180px;
    display: inline-block;
    padding: 50px 20px 30px 20px;
    background: #ffffff;
    color: #333333;
    margin-top: 50px;
    text-align: center;
    opacity: 0;
    -webkit-animation: moveIn 1s 3.1s ease forwards;
    animation: moveIn 1s 3.1s ease forwards; }
    .about .profile-card .profile-bio p {
      font-size: 15px;
      width: 300px;
      padding: 0 0 0 20px;
      text-align: start;
      color: #494949; }
    .about .profile-card .profile-bio h5 {
      text-align: end;
      padding: 0 20px 0; }
  .about .profile-social-links {
    width: 218px;
    display: inline-block;
    float: right;
    margin: 0px;
    padding: 15px 20px;
    background: #ffffff;
    margin-top: 50px;
    text-align: center;
    opacity: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-animation: moveIn 1s 3.1s ease forwards;
    animation: moveIn 1s 3.1s ease forwards; }
  .about .profile-social-links li {
    list-style: none;
    margin: -5px 0px 0px 0px;
    padding: 0px;
    float: left;
    width: 33.3%;
    text-align: center; }
  .about .profile-social-links li a {
    display: inline-block;
    width: 24px;
    height: 24px;
    padding: 6px;
    position: relative;
    overflow: hidden !important;
    -webkit-border-radius: 50%;
    border-radius: 50%; }
  .about .profile-social-links li a img {
    position: relative;
    z-index: 1; }
  .about .profile-social-links li a:before {
    display: block;
    content: '';
    background: rgba(0, 0, 0, 0.3);
    position: absolute;
    left: 0px;
    top: 0px;
    width: 36px;
    height: 36px;
    opacity: 1;
    -webkit-transition: transform 0.4s ease, opacity 1s ease-out;
    transition: transform 0.4s ease, opacity 1s ease-out;
    -webkit-transform: scale3d(0, 0, 1);
    transform: scale3d(0, 0, 1);
    -webkit-border-radius: 50%;
    border-radius: 50%; }
  .about .profile-social-links li a:hover:before {
    -webkit-animation: ripple 1s ease forwards;
    animation: ripple 1s ease forwards; }
  .about .profile-social-links li a img,
  .about .profile-social-links li a svg {
    width: 24px; }
  @media screen and (min-aspect-ratio: 4 / 3) {
    .about body {
      background-size: 100% auto; }
    .about body:before {
      width: 0px; }
    @-webkit-keyframes puff {
      0% {
        top: 100%;
        width: 0px;
        padding-bottom: 0px; }
      100% {
        top: 50%;
        width: 100%;
        padding-bottom: 100%; } }
    @keyframes puff {
      0% {
        top: 100%;
        width: 0px;
        padding-bottom: 0px; }
      100% {
        top: 50%;
        width: 100%;
        padding-bottom: 100%; } } }
  @media screen and (min-height: 480px) {
    .about .profile-card {
      -webkit-animation: init 0.5s 0.2s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, moveDown 1s 0.8s cubic-bezier(0.6, -0.28, 0.735, 0.045) forwards, moveUp 1s 1.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards, materia_portrait 0.5s 2.7s cubic-bezier(0.86, 0, 0.07, 1) forwards;
      animation: init 0.5s 0.2s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, moveDown 1s 0.8s cubic-bezier(0.6, -0.28, 0.735, 0.045) forwards, moveUp 1s 1.8s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards, materia_portrait 0.5s 2.7s cubic-bezier(0.86, 0, 0.07, 1) forwards; }
    .about .profile-card header {
      width: auto;
      height: auto;
      padding: 30px 20px;
      display: block;
      float: none;
      border-right: none; }
    .about .profile-card .profile-bio {
      width: auto;
      height: auto;
      padding: 15px 20px 30px 20px;
      display: block;
      float: none; }
    .about .profile-social-links {
      width: 100%;
      display: block;
      float: none; } }
  @media screen and (min-aspect-ratio: 4 / 3) {
    .about body {
      background-size: 100% auto; }
    .about body:before {
      width: 0px;
      -webkit-animation: puff_landscape 0.5s 1.8s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, borderRadius 0.2s 2.3s linear forwards;
      animation: puff_landscape 0.5s 1.8s cubic-bezier(0.55, 0.055, 0.675, 0.19) forwards, borderRadius 0.2s 2.3s linear forwards; } }

@-webkit-keyframes init {
  0% {
    width: 0px;
    height: 0px; }
  100% {
    width: 56px;
    height: 56px;
    margin-top: 0px;
    opacity: 1; } }

@keyframes init {
  0% {
    width: 0px;
    height: 0px; }
  100% {
    width: 56px;
    height: 56px;
    margin-top: 0px;
    opacity: 1; } }

@-webkit-keyframes puff_portrait {
  0% {
    top: 100%;
    height: 0px;
    padding: 0px; }
  100% {
    top: 50%;
    height: 100%;
    padding: 0px 100%; } }

@keyframes puff_portrait {
  0% {
    top: 100%;
    height: 0px;
    padding: 0px; }
  100% {
    top: 50%;
    height: 100%;
    padding: 0px 100%; } }

@-webkit-keyframes puff_landscape {
  0% {
    top: 100%;
    width: 0px;
    padding-bottom: 0px; }
  100% {
    top: 50%;
    width: 100%;
    padding-bottom: 100%; } }

@keyframes puff_landscape {
  0% {
    top: 100%;
    width: 0px;
    padding-bottom: 0px; }
  100% {
    top: 50%;
    width: 100%;
    padding-bottom: 100%; } }

@-webkit-keyframes borderRadius {
  0% {
    -webkit-border-radius: 50%; }
  100% {
    -webkit-border-radius: 0px; } }

@keyframes borderRadius {
  0% {
    -webkit-border-radius: 50%; }
  100% {
    border-radius: 0px; } }

@-webkit-keyframes moveDown {
  0% {
    top: 50%; }
  50% {
    top: 40%; }
  100% {
    top: 100%; } }

@keyframes moveDown {
  0% {
    top: 50%; }
  50% {
    top: 40%; }
  100% {
    top: 100%; } }

@-webkit-keyframes moveUp {
  0% {
    background: #ffb300;
    top: 100%; }
  50% {
    top: 40%; }
  100% {
    top: 50%;
    background: #e0e0e0; } }

@keyframes moveUp {
  0% {
    background: #ffb300;
    top: 100%; }
  50% {
    top: 40%; }
  100% {
    top: 45%;
    background: #e0e0e0; } }

@-webkit-keyframes materia_landscape {
  0% {
    background: #e0e0e0; }
  50% {
    -webkit-border-radius: 4px; }
  100% {
    width: 440px;
    height: 280px;
    background: #ffffff;
    -webkit-border-radius: 4px; } }

@keyframes materia_landscape {
  0% {
    background: #e0e0e0; }
  50% {
    border-radius: 4px; }
  100% {
    width: 440px;
    height: 280px;
    background: #ffffff;
    border-radius: 4px; } }

@-webkit-keyframes materia_portrait {
  0% {
    background: #e0e0e0; }
  50% {
    -webkit-border-radius: 4px; }
  100% {
    width: 550px;
    height: 550px;
    background: #ffffff;
    -webkit-border-radius: 4px; } }

@keyframes materia_portrait {
  0% {
    background: #e0e0e0; }
  50% {
    border-radius: 4px; }
  100% {
    width: 550px;
    height: 600px;
    background: #ffffff;
    border-radius: 4px; } }

@-webkit-keyframes moveIn {
  0% {
    margin-top: 50px;
    opacity: 0; }
  100% {
    opacity: 1;
    margin-top: -20px; } }

@keyframes moveIn {
  0% {
    margin-top: 50px;
    opacity: 0; }
  100% {
    opacity: 1;
    margin-top: -20px; } }

@-webkit-keyframes scaleIn {
  0% {
    -webkit-transform: scale(0); }
  100% {
    -webkit-transform: scale(1); } }

@keyframes scaleIn {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1); } }

@-webkit-keyframes ripple {
  0% {
    transform: scale3d(0, 0, 0); }
  50%,
  100% {
    -webkit-transform: scale3d(1, 1, 1); }
  100% {
    opacity: 0; } }

@keyframes ripple {
  0% {
    transform: scale3d(0, 0, 0); }
  50%,
  100% {
    transform: scale3d(1, 1, 1); }
  100% {
    opacity: 0; } }

@-webkit-keyframes gradient_flux {
  0% {
    background-position: 66% 0%; }
  50% {
    background-position: 35% 100%; }
  100% {
    background-position: 66% 0%; } }

@-moz-keyframes gradient_bfgradient_flux {
  0% {
    background-position: 66% 0%; }
  50% {
    background-position: 35% 100%; }
  100% {
    background-position: 66% 0%; } }

@keyframes gradient_flux {
  0% {
    background-position: 66% 0%; }
  50% {
    background-position: 35% 100%; }
  100% {
    background-position: 66% 0%; } }
