@font-face {
  font-family: grotesque;
  src: url("../../assets/fonts/DarkerGrotesque-Regular.ttf"); }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  color: #2c2c2c;
  font-family: grotesque;
  margin: 0;
  padding: 0; }

.root__header__logoName-name {
  font-size: 25px;
  margin-bottom: 5px; }

.drawer__title {
  font-size: 20px;
  text-align: center;
  margin: 60px 0 0 0;
  border-top: 1px solid #f4f4f4;
  border-bottom: 1px solid #f4f4f4;
  padding: 5px; }

.switch__header {
  font-size: 23px;
  text-align: center;
  margin: 0 10px; }

.quote__author {
  font-size: 18px;
  color: darkgray; }

.quote__quote {
  text-align: left;
  width: 300px;
  font-size: 20px; }

.main__greeting-h1 {
  color: white; }
