.landing-main,
.landing-main-show {
  display: flex;
  flex-direction: row; }

.landing-header,
.landing-header-initial-1,
.landing-header-initial-2 {
  font-size: 40px;
  color: white;
  text-align: center;
  font-weight: 100;
  overflow: hidden;
  border-right: 0.1rem solid #3a3a3a;
  white-space: nowrap;
  margin: 3rem auto 0 auto;
  letter-spacing: 2px;
  padding-right: 0.5rem;
  animation: typing 1.5s steps(15, end), blink-caret 0.9s step-end infinite; }

.landing-header-initial-1 {
  animation: typing-1 1s steps(10, end), blink-caret 0.9s step-end infinite; }

.landing-header-initial-2 {
  animation: typing-2 2.3s steps(20, end), blink-caret 0.9s step-end infinite; }

.landing-main__title-show,
.landing-main__title {
  color: #e9e9e9;
  font-size: 35px;
  align-self: center;
  opacity: 0;
  transform: translate3d(30px, 30px, 0);
  transition: 0.5s linear;
  font-weight: light; }

.landing-main__title-show {
  transform: translate3d(50px, 30px, 0);
  opacity: 1; }

.landing-main__header {
  display: flex; }

@keyframes typing {
  from {
    width: 0; }
  to {
    width: 9.5rem; } }

@keyframes typing-1 {
  from {
    width: 0; }
  to {
    width: 7rem; } }

@keyframes typing-2 {
  from {
    width: 0; }
  to {
    width: 15rem; } }

@keyframes blink-caret {
  from,
  to {
    border-color: transparent; }
  50% {
    border-color: #dfdfdf; } }
