@font-face {
  font-family: grotesque;
  src: url("../../assets/fonts/DarkerGrotesque-Regular.ttf"); }

.root {
  overflow: hidden;
  grid-template-columns: minmax(10em, 2fr) minmax(300px, 400px) minmax(300px, 400px) minmax(10em, 2fr);
  grid-template-areas: "header header header header" "...... main main  ......" "...... main main  ......" "...... main main  ......" "...... main main  ......" "...... main main  ......" "footer footer footer footer"; }
  .root__header {
    grid-area: header;
    background-color: #f7f7f7; }
  .root__main {
    grid-area: main;
    background-color: rgba(0, 0, 0, 0.03); }
  .root__footer {
    grid-area: footer;
    background-color: #00000004; }
